import { VerifyDocumentInterface } from "../../../actions/users";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { education_category } from "../../../interfaces";
import { SESSION_PLAN_KEYS } from "../../../interfaces/application";
import {
  countDocumentByStatus,
  FC_documentFilterByCenter,
  FC_documentFilterByDistrict,
  FC_documentFilterByRoom,
  getDocByDocId,
  isDocumentValidationOpen,
} from "../../../interfaces/document_validation";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
