export enum DocCheckingContentType {
  names = "names",
  documents = "documents",
}

interface AppProps {
  auth: Auth;
  locations: RoomLocationInterface[];
  documentReducer: DocumentReducerInterface;
  education_categories: education_category[] | null;
  verifyDocument: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  getCandidatesInInstitute: Function;
  getAllRoom: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppState {
  loading: boolean;
  loadingCandidates: boolean;
  loadingRequest: boolean;
  loadingImage: boolean;
  viewDoc: boolean;
  viewCandidate: boolean;
  district: DistrictInterface[] | null;
  thisEducation: education_category | null;
  candidateList: CandidatesInterface[];
  selectedSetting: DocCheckingContentType | null;
  roomTables: LocationRoomTableInterface[] | null;
  distinctDistricts: LocationRoomTableInterface[] | null;
  thisDistrict: LocationRoomTableInterface | null;
  thisCenter: LocationRoomTableInterface | null;
  thisRoom: LocationRoomTableInterface | null;
  settings: {
    DocCheckingContentType: DocCheckingContentType;
    caption: string;
  }[];
  filterByCategory: string;
  filterByCategoryId: string;
  filterBySubCategory: string;
  filterBySubCategoryId: string;
  responseMsg: string;
  response: "approved" | "rejected";
  isTeachingDocument: boolean;
  hasDisabilityDocument: boolean;
  error: { element: string; msg: string };
  document_id: string;
  document_index: number | null;
  candidate_id: string;
  actionTaken: "" | "approval" | "rejection";
}

class DocumentApplChecking extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadingCandidates: true,
      loadingRequest: false,
      loadingImage: true,
      viewDoc: false,
      viewCandidate: false,
      district: null,
      thisEducation: null,
      candidateList: [],
      selectedSetting: DocCheckingContentType.documents,
      roomTables: null,
      distinctDistricts: null,
      thisDistrict: null,
      thisCenter: null,
      thisRoom: null,
      settings: [
        {
          DocCheckingContentType: DocCheckingContentType.documents,
          caption: "View by Document",
        },
        {
          DocCheckingContentType: DocCheckingContentType.names,
          caption: "View by Candidate",
        },
      ],
      filterByCategory: "",
      filterByCategoryId: "",
      filterBySubCategory: "",
      filterBySubCategoryId: "",
      responseMsg: "",
      response: "approved",
      document_id: "",
      candidate_id: "",
      error: {
        element: "",
        msg: "",
      },
      document_index: null,
      isTeachingDocument: false,
      hasDisabilityDocument: false,
      actionTaken: "",
    };
  }

  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  setLoadingImage = (state: boolean) => {
    this.setState({ loadingImage: state });
  };

  setLoadingCandidates = (state: boolean) => {
    this.setState({ loadingCandidates: state });
  };

  setThisDistrict = (district: LocationRoomTableInterface | null) => {
    this.setState({ thisDistrict: district });
  };

  setThisCenter = (center: LocationRoomTableInterface | null) => {
    this.setState({ thisCenter: center });
  };

  setThisRoom = (room: LocationRoomTableInterface | null) => {
    this.setState({ thisRoom: room });
  };

  setFilterByCategory = (category: string) => {
    this.setState({ filterByCategory: category });
  };

  setFilterByCategoryId = (category_id: string) => {
    this.setState({ filterByCategoryId: category_id });
  };

  setFilterBySubCategory = (sub_category: string) => {
    this.setState({ filterBySubCategory: sub_category });
  };

  setFilterBySubCategoryId = (sub_category_id: string) => {
    this.setState({ filterBySubCategoryId: sub_category_id });
  };

  setCandidateId = (data: string) => {
    this.setState({ candidate_id: data });
  };

  setViewDoc = (state: boolean) => {
    this.setState({ viewDoc: state });
  };

  setViewCandidate = (state: boolean) => {
    this.setState({ viewCandidate: state });
  };

  setDocument_index = (data: number | null) => {
    this.setState({ document_index: data });
  };

  setDocumentId = (data: string) => {
    this.setState({ document_id: data });
  };

  setResponseMsg = (data: string) => {
    this.setState({ responseMsg: data });
  };

  setResponse = (data: "approved" | "rejected") => {
    this.setState({ response: data, isTeachingDocument: false });
  };

  setError = (data: { element: string; msg: string }) => {
    this.setState({ error: data });
  };

  setActionTaken = (data: "" | "approval" | "rejection") => {
    this.setState({ actionTaken: data });
  };

  setIsTeachingDocument = (state: boolean) => {
    this.setState({ isTeachingDocument: state });
  };

  setHasDisabilityDocument = (state: boolean) => {
    this.setState({ hasDisabilityDocument: state });
  };

  loadDistrict = async (callback: Function) => {
    callback(true, "");
    try {
      const res = await axios.get<DistrictInterface[]>(
        `${API.APPLICATION}/district`
      );
      this.setState({ district: res.data });
      callback(false, "");
    } catch (error) {
      console.error({ code_error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };

  loadDefaultData = (
    status: boolean,
    msg: string,
    candidatesInInstitute_data?: CandidatesInterface[] | null
  ) => {
    if (!status && isEmptyOrSpaces(msg)) {
      this.setLoading(true);
      let list_1: CandidatesInterface[] = candidatesInInstitute_data
        ? candidatesInInstitute_data.filter(
            (candidate) => candidate.details.length > 0
          )
        : [];
      let mailList: string[] = [];
      let list_2: CandidatesInterface[] = list_1.filter((candidate) =>
        candidate.details.filter((item) => item.files !== "")
      );
      let list: CandidatesInterface[] = [];
      for (let i = 0; i < list_2.length; i++) {
        if (mailList.includes(list_2[i].email)) continue;
        mailList.push(list_2[i].email);
        list.push(list_2[i]);
      }

      this.setState({
        candidateList: list,
        roomTables: LocationsInArray(this.props.locations),
      });
      if (
        !this.state.distinctDistricts ||
        !this.state.distinctDistricts.length
      ) {
        var flags: string[] = [],
          output: LocationRoomTableInterface[] = [],
          l: number = LocationsInArray(this.props.locations).length,
          i: number;
        for (i = 0; i < l; i++) {
          if (
            flags.includes(
              LocationsInArray(this.props.locations)[i].district_id
            )
          )
            continue;
          flags.push(LocationsInArray(this.props.locations)[i].district_id);
          output.push(LocationsInArray(this.props.locations)[i]);
        }
        this.setState({
          distinctDistricts: output,
        });
      }
      this.setLoading(false);
    }
  };

  loadCandidatesInInstitute = (
    status: boolean,
    education_categories_data?: education_category[] | null
  ) => {
    // LOADING CANDIDATES BY EDICATION CATEGORY ===========================
    if (
      !status &&
      education_categories_data &&
      education_categories_data.length > 0 &&
      this.state.thisEducation === null
    ) {
      this.setState({ thisEducation: education_categories_data[0] });
      // console.log("getCandidatesInInstitute");
      if (
        (this.props.documentReducer.candidatesInInstitute == null ||
          this.props.documentReducer.candidatesInInstitute.length <= 0) &&
        this.props.auth.application_session
      ) {
        this.props.getCandidatesInInstitute(
          education_categories_data[0].education_institute_id,
          this.props.auth.application_session.session_plan_id,
          (
            status: boolean,
            msg: string,
            data?: CandidatesInterface[] | null
          ) => {
            this.setLoadingCandidates(status);
            if (!status && msg.length > 0) {
              console.error({ loadingCandidatesError: msg });
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
              this.setLoadingCandidates(false);
            }
            this.loadDefaultData(status, msg, data);
          }
        );
      } else {
        this.setLoadingCandidates(false);
        this.loadDefaultData(
          false,
          "",
          this.props.documentReducer.candidatesInInstitute
        );
      }
    }
  };

  componentDidMount = () => {
    // LOADING DOCUMENT CATEGORIES ===============================
    if (
      (this.props.documentReducer.documentCategory === null ||
        this.props.documentReducer.documentCategory.length <= 0) &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      this.props.getDocumentCategory();
    }

    // LOADING DOCUMETN SUB CATEGORYIES =========================
    if (
      (this.props.documentReducer.documentSubCategory === null ||
        this.props.documentReducer.documentSubCategory.length <= 0) &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      this.props.getDocumentSubCategory();
    }

    // LOADING DOCUMETN FIELDS =========================
    if (
      (this.props.documentReducer.documentFields === null ||
        this.props.documentReducer.documentFields.length <= 0) &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      this.props.getDocumentFields();
    }

    // LOADING DISTRICTS =========================================
    if (
      (!this.state.district || this.state.district.length <= 0) &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      this.loadDistrict((status: boolean, message: string) => {
        if (!status) {
          if (message.length > 0) {
            this.props.PushPopupAlert({
              title: message,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }

    // LOADING EDICATION CATEGORY =========================================
    if (
      (this.props.education_categories === null ||
        this.props.education_categories.length <= 0) &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      this.props.loadEducationCategories(this.loadCandidatesInInstitute);
    } else if (
      this.state.thisEducation === null &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      this.loadCandidatesInInstitute(false, this.props.education_categories);
    }

    // LOADING LOCATIONS PROVINCE, DISTRICT, CENTER, ROOM ==================
    if (
      !this.props.locations.length &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      this.props.getAllRoom((status: boolean, msg: string) => {
        if (!status && msg.length > 0) {
          console.error(msg);
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else {
      this.setLoading(false);
    }
  };

  loadCandidates = (education: education_category) => {
    //   FOR EDUCATION CATEGORY CHANGED ===========
    if (
      (this.state.thisEducation &&
        this.state.thisEducation.education_institute_id !==
          education.education_institute_id) ||
      this.state.thisEducation === null
    ) {
      this.props.getCandidatesInInstitute(
        education.education_institute_id,
        (status: boolean, msg: string, data?: CandidatesInterface[]) => {
          this.setLoadingCandidates(status);
          if (!status && msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
            console.error({ loadingCandidatesError: msg });
          }
        }
      );
    }
    this.setState({ thisEducation: education });
  };

  countById = (
    candidates: CandidatesInterface[],
    filterType: "docCategory" | "docSubCategory" | "docStatus" | "DISTRICT",
    filterValue: string,
    filterStatus: "waiting" | "rejected" | "approved" = "waiting"
  ): number => {
    let result: CandidatesInterface[] = candidates;
    let total: number = 0;

    if (filterType === "docCategory") {
      if (filterValue === "all") {
        result = candidates.filter(
          (candidate) =>
            candidate.details.length > 0 &&
            candidate.details.filter(
              (details) =>
                details.files &&
                details.files.length > 0 &&
                details.validation_status === filterStatus
            )
        );
        total = 0;
        for (var i = 0; i < result.length; i++) {
          for (var r = 0; r < result[i].details.length; r++) {
            total +=
              result[i].details[r].files &&
              result[i].details[r].files.length > 0 &&
              result[i].details[r].validation_status === filterStatus
                ? 1
                : 0;
          }
        }
      } else {
        result = candidates.filter(
          (candidate) =>
            candidate.details.length > 0 &&
            candidate.details.filter(
              (details) =>
                details.category_id === filterValue &&
                details.files &&
                details.files.length > 0 &&
                details.validation_status === filterStatus
            )
        );
        total = 0;
        for (i = 0; i < result.length; i++) {
          for (r = 0; r < result[i].details.length; r++) {
            total +=
              result[i].details[r].category_id === filterValue &&
              result[i].details[r].files &&
              result[i].details[r].files.length > 0 &&
              result[i].details[r].validation_status === filterStatus
                ? 1
                : 0;
          }
        }
      }
    } else if (filterType === "docSubCategory") {
      result = candidates.filter((candidate) =>
        candidate.details.filter(
          (details) =>
            details.sub_category_id === filterValue &&
            details.files &&
            details.files.length > 0 &&
            details.validation_status === filterStatus
        )
      );

      total = 0;
      for (var k = 0; k < result.length; k++) {
        for (var j = 0; j < result[k].details.length; j++) {
          total +=
            result[k].details[j].sub_category_id === filterValue &&
            result[k].details[j].files &&
            result[k].details[j].files.length > 0 &&
            result[k].details[j].validation_status === filterStatus
              ? 1
              : 0;
        }
      }
    } else if (filterType === "DISTRICT" && this.state.roomTables) {
      let centersIdInDistrict: string[] = [];
      // var flags: string[] = [],
      let output: LocationRoomTableInterface[] = [],
        l: number = this.state.roomTables.filter(
          (d) => d.district_id === filterValue
        ).length;
      // i: number;
      for (i = 0; i < l; i++) {
        if (centersIdInDistrict.includes(this.state.roomTables[i].center_id))
          continue;
        centersIdInDistrict.push(this.state.roomTables[i].center_id);
        output.push(this.state.roomTables[i]);
      }
      result = candidates.filter(
        (candidate) =>
          candidate.exams &&
          candidate.exams.filter((c) =>
            centersIdInDistrict.includes(c.center_id)
          )
      );

      total = 0;
      for (k = 0; k < result.length; k++) {
        for (j = 0; j < result[k].details.length; j++) {
          total +=
            result[k].details[j].files &&
            result[k].details[j].files.length > 0 &&
            result[k].details[j].validation_status === filterStatus
              ? 1
              : 0;
        }
      }
    }

    return total;
  };

  filterData = (
    filterType:
      | "docCategory"
      | "docSubCategory"
      | "docStatus"
      | "DISTRICT"
      | "CENTER"
      | "ROOM",
    filterValue: string
  ) => {
    let result: CandidatesInterface[] = [];
    if (this.props.documentReducer.candidatesInInstitute) {
      let data: CandidatesInterface[] = [];
      let action: boolean = true;
      for (
        let i = 0;
        i < this.props.documentReducer.candidatesInInstitute.length;
        i++
      ) {
        if (
          this.props.documentReducer.candidatesInInstitute[i].details.length !==
          0
        ) {
          action = true;
          for (
            let j = 0;
            j <
            this.props.documentReducer.candidatesInInstitute[i].details.length;
            j++
          ) {
            if (
              this.props.documentReducer.candidatesInInstitute[i].details[j]
                .files.length > 0 &&
              action
            ) {
              data.push(this.props.documentReducer.candidatesInInstitute[i]);
              action = false;
            }
          }
        }
      }

      let mailList: string[] = [];
      let _data: CandidatesInterface[] = [];
      for (let i = 0; i < data.length; i++) {
        if (mailList.includes(data[i].email)) continue;
        mailList.push(data[i].email);
        _data.push(data[i]);
      }
      result = _data;

      // FILTER BY DISTRICT ID ====================================
      if (filterType === "DISTRICT" && this.state.roomTables) {
        result =
          filterValue !== "all"
            ? FC_documentFilterByDistrict(
                this.props.documentReducer.candidatesInInstitute,
                this.state.roomTables,
                filterValue
              )
            : result;
        this.setState({
          filterByCategory: "",
          filterByCategoryId: "",
          filterBySubCategory: "",
          filterBySubCategoryId: "",
        });
      }

      // FILTER BY CENTER ID =============================================
      if (
        filterType === "CENTER" &&
        this.state.roomTables &&
        this.state.candidateList &&
        this.state.thisDistrict
      ) {
        result = FC_documentFilterByDistrict(
          this.props.documentReducer.candidatesInInstitute,
          this.state.roomTables,
          this.state.thisDistrict.district_id
        );
        console.log(
          FC_documentFilterByDistrict(
            this.props.documentReducer.candidatesInInstitute,
            this.state.roomTables,
            filterValue
          )
        );
        result = FC_documentFilterByCenter(result, filterValue);
        this.setState({
          filterByCategory: "",
          filterByCategoryId: "",
          filterBySubCategory: "",
          filterBySubCategoryId: "",
        });
        // console.log("FILTER_____", centersIdInDistrict);
      }

      // FILTER BY ROOM ID =============================================
      if (
        filterType === "ROOM" &&
        this.state.roomTables &&
        this.state.candidateList &&
        this.state.thisDistrict &&
        this.state.thisCenter
      ) {
        result = FC_documentFilterByDistrict(
          this.props.documentReducer.candidatesInInstitute,
          this.state.roomTables,
          this.state.thisDistrict.district_id
        );
        result = FC_documentFilterByCenter(
          result,
          this.state.thisCenter.center_id
        );
        result = FC_documentFilterByRoom(result, filterValue);
        this.setState({
          filterByCategory: "",
          filterByCategoryId: "",
          filterBySubCategory: "",
          filterBySubCategoryId: "",
        });
      }
    }

    this.setState({
      candidateList: result,
    });
  };

  FC_shortListing = async (candidate_id: string) => {
    const thisCandidate = getDocByDocId(
      this.state.candidateList,
      this.state.candidate_id,
      ""
    ).user;
    if (thisCandidate) {
      try {
        const res = await axios.patch(
          `${API.APPLICATION}/doccheckshortlist/${candidate_id}`,
          {
            email: thisCandidate.email,
            phone_number: thisCandidate.phone_number,
          }
        );
        console.log("====================================");
        console.log({ res });
        console.log("====================================");
      } catch (error) {
        console.error({ error });
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
    }
  };

  handleVerify = async () => {
    if (
      this.state.candidate_id.length > 0 &&
      this.state.document_id.length > 0 &&
      this.state.response
    ) {
      if (
        this.state.response === "rejected" &&
        !this.state.responseMsg.length
      ) {
        return this.setState({
          error: {
            element: "comment",
            msg: "Reason of rejection is required",
          },
          loadingRequest: false,
        });
      }

      this.setState({
        loadingRequest: true,
      });

      const thisCandidate = getDocByDocId(
        this.state.candidateList,
        this.state.candidate_id,
        this.state.document_id
      ).user!;
      const api_body: VerifyDocumentInterface = {
        user_id: this.state.candidate_id,
        doc_id: this.state.document_id,
        validation_status:
          this.state.response === "approved"
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        validated_by: {
          user_id: this.props.auth.user.user_id,
          comment: this.state.responseMsg,
          validation_status:
            this.state.response === "approved"
              ? DocValidationStatusInterface.APPROVED
              : DocValidationStatusInterface.REJECTED,
        },
        in_education: this.state.isTeachingDocument
          ? this.state.isTeachingDocument
          : thisCandidate.in_education,
        disability: this.state.hasDisabilityDocument
          ? this.state.hasDisabilityDocument
          : thisCandidate.disability,
      };
      const api_redux: VerifyDocumentInterface = {
        user_id: this.state.candidate_id,
        doc_id: this.state.document_id,
        validation_status:
          this.state.response === "approved"
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        validated_by: {
          user_id: this.props.auth.user.user_id,
          comment: this.state.responseMsg,
          validation_status:
            this.state.response === "approved"
              ? DocValidationStatusInterface.APPROVED
              : DocValidationStatusInterface.REJECTED,
          date: "",
        },
        in_education: this.state.isTeachingDocument
          ? this.state.isTeachingDocument
          : thisCandidate.in_education,
        disability: this.state.hasDisabilityDocument
          ? this.state.hasDisabilityDocument
          : thisCandidate.disability,
      };

      try {
        const res = await axios.post<VerifyDocumentInterface>(
          `${API.AUTH}/validateuserdoc`,
          api_body
        );
        if (res) {
          await this.FC_shortListing(this.state.candidate_id);
          this.props.verifyDocument(api_redux);
          this.props.PushPopupAlert({
            title: `Document ${this.state.response} successful`,
            theme: PopupAlertTheme.success,
          });

          this.setState({
            response: "approved",
            responseMsg: "",
            actionTaken: "",
            hasDisabilityDocument: false,
            isTeachingDocument: false,
          });
        }
      } catch (error) {
        console.error(error);
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      this.setState({ loadingRequest: false });
    }
  };

  cleanFilter = () => {
    if (this.state.thisRoom) {
      this.filterData("ROOM", this.state.thisRoom.room_id);
    } else if (this.state.thisCenter) {
      this.filterData("CENTER", this.state.thisCenter.center_id);
    } else if (this.state.thisDistrict) {
      this.filterData("DISTRICT", this.state.thisDistrict.district_id);
    } else {
      let result: CandidatesInterface[] = [];
      if (this.props.documentReducer.candidatesInInstitute) {
        let data: CandidatesInterface[] = [];
        let action: boolean = true;
        for (
          let i = 0;
          i < this.props.documentReducer.candidatesInInstitute.length;
          i++
        ) {
          if (
            this.props.documentReducer.candidatesInInstitute[i].details
              .length !== 0
          ) {
            action = true;
            for (
              let j = 0;
              j <
              this.props.documentReducer.candidatesInInstitute[i].details
                .length;
              j++
            ) {
              if (
                this.props.documentReducer.candidatesInInstitute[i].details[j]
                  .files !== null &&
                this.props.documentReducer.candidatesInInstitute[i].details[j]
                  .files.length > 0 &&
                action
              ) {
                data.push(this.props.documentReducer.candidatesInInstitute[i]);
                action = false;
              }
            }
          }
        }

        let mailList: string[] = [];
        let _data: CandidatesInterface[] = [];
        for (let i = 0; i < data.length; i++) {
          if (mailList.includes(data[i].email)) continue;
          mailList.push(data[i].email);
          _data.push(data[i]);
        }
        result = _data;
      }
      this.setState({
        candidateList: result,
        filterByCategory: "",
        filterByCategoryId: "",
        filterBySubCategory: "",
        filterBySubCategoryId: "",
      });
    }
  };

  render() {
    if (
      this.state.loading === true &&
      isDocumentValidationOpen(this.props.auth.application_session)
    ) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading...
        </div>
      );
    }

    return (
      <div className="w-full my-5">
        {/* header title */}
        {isDocumentValidationOpen(this.props.auth.application_session) && (
          <div className="flex flex-row items-start gap-4 mb-2">
            <ImFolderOpen className="text-blue-700 w-20 h-20 md:block hidden" />
            <div className="w-full">
              <div className="flex justify-between w-full">
                <div className="flex flex-col gap-1 justify-between">
                  <h1 className="md:text-4xl text-2xl font-semibold text-blue-600">
                    Document validation - Applied candidate(s)
                  </h1>

                  <div className="flex flex-row items-center md:gap-3 gap-2">
                    {/* EDUCATION CATEGORY sections */}
                    <section className="flex items-start md:items-center justify-start md:justify-end gap-2 mt-1">
                      {this.props.education_categories &&
                        this.props.education_categories.length > 0 &&
                        this.props.education_categories.map(
                          (education, index) => (
                            <section
                              key={education.education_institute_id + index}
                              className={`w-auto ${
                                this.state.loadingRequest ||
                                this.state.loadingCandidates
                                  ? "cursor-wait"
                                  : "cursor-pointer"
                              } ${
                                this.state.thisEducation &&
                                education.education_institute_id ===
                                  this.state.thisEducation
                                    .education_institute_id
                                  ? "bg-blue-500 text-white font-bold"
                                  : "bg-gray-100 text-blue-800"
                              } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex items-center animate__animated animate__zoomIn`}
                              onClick={() =>
                                !this.state.loadingRequest &&
                                !this.state.loadingCandidates &&
                                this.loadCandidates(education)
                              }
                            >
                              <section className="flex-1">
                                <MdBrightness1 className="inline-block mr-1 text-xs" />
                                <span>
                                  {education.education_category_title}
                                </span>
                              </section>
                            </section>
                          )
                        )}
                    </section>
                    {/* end EDUCATION CATEGORY sections */}
                  </div>
                </div>
                <div className="text-right md:flex flex-col hidden">
                  {this.state.thisDistrict && (
                    <div className="text-gray-700">
                      District:{" "}
                      <span className="font-semibold">
                        {this.state.thisDistrict.district_name}
                      </span>
                    </div>
                  )}
                  {this.state.thisCenter && (
                    <div className="text-gray-700">
                      Center:{" "}
                      <span className="font-semibold">
                        {this.state.thisCenter.center_name}
                      </span>
                    </div>
                  )}
                  {this.state.thisRoom && (
                    <div className="text-gray-700">
                      Room:{" "}
                      <span className="font-semibold">
                        {this.state.thisRoom.room_name}
                      </span>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        )}
        {/* end header title */}

        {/* -------------------------------------------------------------- */}
        <section>
          {/* Document validation is closed ================================================ */}
          {!isDocumentValidationOpen(this.props.auth.application_session) && (
            <section className="py-10 flex flex-col items-center rounded border border-gray-300 bg-white text-2xl font-medium text-yellow-800 animate__animated animate__pulse">
              <div className="flex flex-col items-center">
                <IoWarning className="w-32 h-32" />
                Document validation session is closed!
              </div>

              {this.props.auth.application_session &&
                this.props.auth.application_session.plans &&
                this.props.auth.application_session.plans.find(
                  (item) =>
                    item.type_key === SESSION_PLAN_KEYS.DOCUMENT_VALIDATION
                ) && (
                  <div className="flex flex-row items-center justify-center gap-4 mt-5">
                    <span className="font-bold text-gray-900 bg-blue-50 rounded-full px-3 py-1">
                      Start date:{" "}
                      {DATE(
                        this.props.auth.application_session.plans.find(
                          (item) =>
                            item.type_key ===
                            SESSION_PLAN_KEYS.DOCUMENT_VALIDATION
                        )!.start_date,
                        "DD/MM/YYYY"
                      )}
                    </span>
                    <span className="font-bold text-gray-900 bg-blue-50 rounded-full px-3 py-1">
                      Ending date:{" "}
                      {DATE(
                        this.props.auth.application_session.plans.find(
                          (item) =>
                            item.type_key ===
                            SESSION_PLAN_KEYS.DOCUMENT_VALIDATION
                        )!.end_date,
                        "DD/MM/YYYY"
                      )}
                    </span>
                  </div>
                )}
            </section>
          )}
          {/* Document validation is closed ================================================ */}

          {/* ******************************************************************** */}
          {isDocumentValidationOpen(this.props.auth.application_session) && (
            <div className="bg-white shadow rounded border border-gray-2 p-2 w-full overflow-x-auto">
              {/* TABS CONTROLLER ===================================== */}
              {!this.state.viewCandidate && (
                <div className="flex flex-row items-center">
                  {this.state.settings.map((item, i) => (
                    <div
                      key={i}
                      onClick={() => {
                        this.setState({
                          selectedSetting: item.DocCheckingContentType,
                        });
                        this.cleanFilter();
                      }}
                      className={`animate__animated ${
                        this.state.selectedSetting ===
                        item.DocCheckingContentType
                          ? "bg-blue-100 border-blue-600 text-blue-600 animate__zoomIn"
                          : "bg-white border-white text-gray-600"
                      } hover:bg-blue-100 px-4 py-2 cursor-pointer font-bold border-b-4 hover:border-blue-600 hover:text-blue-600 rounded-t mt-1 mx-1`}
                    >
                      {item.caption}
                    </div>
                  ))}
                </div>
              )}
              {this.state.viewCandidate && (
                <div className="flex items-center gap-3 mb-2 text-gray-600">
                  <button
                    className="inline-block p-3 transition rounded-full bg-gray-400 ripple hover:bg-gray-600 focus:outline-none"
                    onClick={() => {
                      this.setState({
                        viewCandidate: false,
                        candidate_id: "",
                      });
                    }}
                  >
                    <IoMdArrowRoundBack className="w-6 h-6 text-gray-50" />
                  </button>

                  <div className="flex flex-col">
                    <span className="text-gray-800 font-semibold text-2xl">
                      {getDocByDocId(
                        this.state.candidateList,
                        this.state.candidate_id,
                        ""
                      ).user
                        ? getDocByDocId(
                            this.state.candidateList,
                            this.state.candidate_id,
                            ""
                          ).user!.fname +
                          " " +
                          getDocByDocId(
                            this.state.candidateList,
                            this.state.candidate_id,
                            ""
                          ).user!.lname
                        : "Unknown user"}
                    </span>
                    <span className="text-gray-600 font-normal text-sm">
                      {getDocByDocId(
                        this.state.candidateList,
                        this.state.candidate_id,
                        ""
                      ).user
                        ? getDocByDocId(
                            this.state.candidateList,
                            this.state.candidate_id,
                            ""
                          ).user!.email
                        : ""}
                    </span>
                  </div>
                </div>
              )}
              {/* END TABS CONTROLLER ===================================== */}

              {/* ################################################### */}

              {/* VIEW LISTS */}
              {!this.state.viewCandidate && (
                <section className="">
                  <FilterForm
                    roomTables={this.state.roomTables}
                    candidateList={this.state.candidateList}
                    distinctDistricts={this.state.distinctDistricts}
                    thisDistrict={this.state.thisDistrict}
                    filterByCategoryId={this.state.filterByCategoryId}
                    filterBySubCategoryId={this.state.filterBySubCategoryId}
                    filterByCategory={this.state.filterByCategory}
                    filterBySubCategory={this.state.filterBySubCategory}
                    setThisDistrict={this.setThisDistrict}
                    filterData={this.filterData}
                    setThisCenter={this.setThisCenter}
                    setThisRoom={this.setThisRoom}
                    setFilterByCategory={this.setFilterByCategory}
                    setFilterByCategoryId={this.setFilterByCategoryId}
                    setFilterBySubCategory={this.setFilterBySubCategory}
                    setFilterBySubCategoryId={this.setFilterBySubCategoryId}
                    loadingRequest={this.state.loadingRequest}
                    loadingCandidates={this.state.loadingCandidates}
                    thisCenter={this.state.thisCenter}
                    selectedSetting={this.state.selectedSetting}
                    documentCategories={this.props.documentReducer}
                    countById={this.countById}
                  />

                  {this.state.selectedSetting ===
                    DocCheckingContentType.names && (
                    <ContentByCandidate
                      loadingCandidates={this.state.loadingCandidates}
                      loadingRequest={this.state.loadingRequest}
                      candidateList={this.state.candidateList}
                      setCandidateId={this.setCandidateId}
                      setViewCandidate={this.setViewCandidate}
                      countByStatus={countDocumentByStatus}
                      countById={this.countById}
                      setLoadingImage={this.setLoadingImage}
                    />
                  )}
                  {this.state.selectedSetting ===
                    DocCheckingContentType.documents && (
                    <ContentByDocument
                      loadingCandidates={this.state.loadingCandidates}
                      loadingRequest={this.state.loadingRequest}
                      filterByCategoryId={this.state.filterByCategoryId}
                      filterBySubCategoryId={this.state.filterBySubCategoryId}
                      candidateList={this.state.candidateList}
                      countByStatus={countDocumentByStatus}
                      countById={this.countById}
                      documentCategories={this.props.documentReducer}
                      setDocumentId={this.setDocumentId}
                      setCandidateId={this.setCandidateId}
                      setViewDoc={this.setViewDoc}
                      document_id={this.state.document_id}
                      candidate_id={this.state.candidate_id}
                      setDocument_index={this.setDocument_index}
                      setLoadingImage={this.setLoadingImage}
                    />
                  )}
                </section>
              )}

              {this.state.viewCandidate &&
                getDocByDocId(
                  this.state.candidateList,
                  this.state.candidate_id,
                  ""
                ).user && (
                  <section className="animate__animated animate__fadeIn">
                    <ApplUserDocuments
                      candidate={
                        getDocByDocId(
                          this.state.candidateList,
                          this.state.candidate_id,
                          ""
                        ).user!
                      }
                      candidateCheckData={null}
                      filterByCategoryId={this.state.filterByCategoryId}
                      filterBySubCategoryId={this.state.filterBySubCategoryId}
                      setDocumentId={this.setDocumentId}
                      setCandidateId={this.setCandidateId}
                      setViewDoc={this.setViewDoc}
                      setDocument_index={this.setDocument_index}
                    />
                  </section>
                )}
            </div>
          )}
        </section>

        {/* -------------------------------------------------------------- */}

        {/* modal control */}
        {this.state.viewDoc &&
          this.state.document_index !== null &&
          getDocByDocId(
            this.state.candidateList,
            this.state.candidate_id,
            this.state.document_id
          ).doc &&
          getDocByDocId(
            this.state.candidateList,
            this.state.candidate_id,
            this.state.document_id
          ).user && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => {}}
              displayClose={false}
              backDropClose={false}
              title=""
              widthSizeClass={ModalSize.maxWidth}
              padding={{ title: false, body: false, footer: false }}
            >
              <ApplFileView
                error={this.state.error}
                doc={
                  getDocByDocId(
                    this.state.candidateList,
                    this.state.candidate_id,
                    this.state.document_id
                  ).doc!
                }
                candidate={
                  getDocByDocId(
                    this.state.candidateList,
                    this.state.candidate_id,
                    this.state.document_id
                  ).user!
                }
                responseMsg={this.state.responseMsg}
                loadingRequest={this.state.loadingRequest}
                setResponseMsg={this.setResponseMsg}
                setResponse={this.setResponse}
                handleVerify={this.handleVerify}
                selectedSetting={this.state.selectedSetting}
                setViewCandidate={this.setViewCandidate}
                setError={this.setError}
                setViewDoc={this.setViewDoc}
                setCandidateId={this.setCandidateId}
                setDocumentId={this.setDocumentId}
                actionTaken={this.state.actionTaken}
                setActionTaken={this.setActionTaken}
                document_index={this.state.document_index}
                setDocument_index={this.setDocument_index}
                candidateList={this.state.candidateList}
                isTeachingDocument={this.state.isTeachingDocument}
                setIsTeachingDocument={this.setIsTeachingDocument}
                hasDisabilityDocument={this.state.hasDisabilityDocument}
                setHasDisabilityDocument={this.setHasDisabilityDocument}
                loadingImage={this.state.loadingImage}
                setLoadingImage={this.setLoadingImage}
              />
            </Modal>
          )}
        {/* end modal control */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  document,
  locations,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
} => {
  return {
    auth: auth,
    documentReducer: document,
    locations: locations.roomLocation,
    education_categories: schoolLevels.education_categories,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    getAllRoom,
    verifyDocument,
    getDocumentCategory,
    getDocumentSubCategory,
    getDocumentFields,
    loadEducationCategories,
    getCandidatesInInstitute,
    PushPopupAlert,
  })(DocumentApplChecking)
);
