import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { FaFilter } from "react-icons/fa";
import { MdClear } from "react-icons/md";
import { DocumentReducerInterface } from "../../../reducers/document";
import {
  CandidatesInterface,
  DistrictInterface,
  DocumentCategoryInterface,
  DocumentSubCategoryInterface,
  LocationRoomTableInterface,
} from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { DocCheckingContentType } from "./DocumentApplChecking";

export enum FilterCategoryType {
  location = "location",
  document = "document",
}

export const filterContent: {
  filterContentType: FilterCategoryType;
  caption: string;
}[] = [
  {
    filterContentType: FilterCategoryType.location,
    caption: "Filter by location",
  },
  {
    filterContentType: FilterCategoryType.document,
    caption: "Filter by document types",
  },
];

const importantDocTypes = (
  thisDocTypes: DocumentCategoryInterface[],
  thisCandidateList: CandidatesInterface[],
  thisCountById: (
    candidates: CandidatesInterface[],
    filterType: "docCategory" | "docSubCategory" | "docStatus" | "DISTRICT",
    filterValue: string,
    filterStatus?: "waiting" | "rejected" | "approved"
  ) => number
): DocumentCategoryInterface[] => {
  let result: DocumentCategoryInterface[] = [];
  // setDocCategories(props.documentCategories.documentCategory);
  for (const key in thisDocTypes) {
    if (Object.prototype.hasOwnProperty.call(thisDocTypes, key)) {
      if (
        thisCountById(
          thisCandidateList,
          "docCategory",
          thisDocTypes[key].user_detail_category_id
        ) > 0
      ) {
        result.push(thisDocTypes[key]);
      }
    }
  }
  return result;
};

interface FilterFormProps {
  roomTables: LocationRoomTableInterface[] | null;
  candidateList: CandidatesInterface[];
  distinctDistricts: LocationRoomTableInterface[] | null;
  thisDistrict: LocationRoomTableInterface | null;
  thisCenter: LocationRoomTableInterface | null;
  setThisDistrict: (district: LocationRoomTableInterface | null) => void;
  filterData: (
    filterType:
      | "docCategory"
      | "docSubCategory"
      | "docStatus"
      | "DISTRICT"
      | "CENTER"
      | "ROOM",
    filterValue: string
  ) => void;
  setThisCenter: (center: LocationRoomTableInterface | null) => void;
  setThisRoom: (room: LocationRoomTableInterface | null) => void;
  loadingRequest: boolean;
  loadingCandidates: boolean;
  selectedSetting: DocCheckingContentType | null;
  documentCategories: DocumentReducerInterface;
  countById: (
    candidates: CandidatesInterface[],
    filterType: "docCategory" | "docSubCategory" | "docStatus" | "DISTRICT",
    filterValue: string,
    filterStatus?: "waiting" | "rejected" | "approved"
  ) => number;
  setFilterByCategory: (data: string) => void;
  filterByCategoryId: string;
  filterBySubCategoryId: string;
  filterByCategory: string;
  filterBySubCategory: string;
  setFilterByCategoryId: (data: string) => void;
  setFilterBySubCategory: (data: string) => void;
  setFilterBySubCategoryId: (data: string) => void;
}

const FilterForm: React.FC<FilterFormProps> = (props) => {
  const [district, setDistrict] = useState<DistrictInterface[] | null>(null);
  const [selectedFilterType, setSelectedFilterType] = useState<
    FilterCategoryType | ""
  >("");
  const [subDocCategories, setSubDocCategories] = useState<
    DocumentSubCategoryInterface[] | null
  >(null);
  const [loading, setLoading] = useState<boolean>(true);

  const loadDistrict = async () => {
    try {
      const res = await axios.get<DistrictInterface[]>(
        `${API.APPLICATION}/district`
      );
      setDistrict(res.data);
    } catch (error) {
      console.log({ code_error: error });
    }
  };

  useEffect(() => {
    if (!district) {
      loadDistrict();
    }
    if (loading) {
      if (props.selectedSetting === DocCheckingContentType.names) {
        setSelectedFilterType(FilterCategoryType.location);
      } else {
        setSelectedFilterType("");
      }
      setLoading(false);
    }

    if (
      props.documentCategories.documentSubCategory &&
      props.candidateList &&
      props.filterByCategoryId !== "all"
    ) {
      let allSubCategory = props.documentCategories.documentSubCategory.filter(
        (item) => item.category_id === props.filterByCategoryId
      );
      let sub_categories: DocumentSubCategoryInterface[] = [];
      for (const key in allSubCategory) {
        if (
          Object.prototype.hasOwnProperty.call(
            props.documentCategories.documentCategory,
            key
          )
        ) {
          if (
            props.countById(
              props.candidateList,
              "docSubCategory",
              allSubCategory[key].user_detail_subcategory_id
            ) > 0
          ) {
            sub_categories.push(allSubCategory[key]);
          }
        }
      }
      setSubDocCategories(sub_categories);
    }
  }, [district, loading, props]);

  const selectDistrict = (district_id: string) => {
    props.distinctDistricts &&
      props.setThisDistrict(
        props.distinctDistricts.filter(
          (district) => district.district_id === district_id
        )[0]
      );
    props.filterData("DISTRICT", district_id);
    props.setThisCenter(null);
    props.setThisRoom(null);
  };

  const selectCenter = (center_id: string) => {
    props.roomTables &&
      props.setThisCenter(
        props.roomTables.filter((center) => center.center_id === center_id)[0]
      );
    props.filterData("CENTER", center_id);
    props.setThisRoom(null);
  };

  const selectRoom = (room_id: string) => {
    props.roomTables &&
      props.setThisRoom(
        props.roomTables.filter((room) => room.room_id === room_id)[0]
      );
    props.filterData("ROOM", room_id);
  };

  const selectDocCategory = (category_id: string) => {
    if (props.documentCategories.documentCategory) {
      let thisCategory = props.documentCategories.documentCategory.find(
        (category) => category.user_detail_category_id === category_id
      );
      props.filterData("docCategory", category_id);
      props.setFilterByCategory(thisCategory ? thisCategory.title : "");
      props.setFilterByCategoryId(
        thisCategory ? thisCategory.user_detail_category_id : category_id
      );
      props.setFilterBySubCategory("");
      props.setFilterBySubCategoryId("");
    }
  };

  const selectSubDocCategory = (sub_category_id: string) => {
    if (props.documentCategories.documentSubCategory) {
      let thisSubCategory = props.documentCategories.documentSubCategory.find(
        (sub_category) =>
          sub_category.user_detail_subcategory_id === sub_category_id
      );
      props.filterData("docSubCategory", sub_category_id);
      props.setFilterBySubCategory(
        thisSubCategory ? thisSubCategory.title : ""
      );
      props.setFilterBySubCategoryId(
        thisSubCategory
          ? thisSubCategory.user_detail_subcategory_id
          : sub_category_id
      );
    }
  };

  const getCenterByDistrict = (
    district_id: string
  ): LocationRoomTableInterface[] => {
    let n: LocationRoomTableInterface[] = [];
    if (props.roomTables) {
      n = props.roomTables.filter(
        (element) => element.district_id === district_id
      );
    }

    let result: LocationRoomTableInterface[] = [],
      _ids: string[] = [];
    for (let i = 0; i < n.length; i++) {
      if (_ids.includes(n[i].center_id)) continue;
      _ids.push(n[i].center_id);
      result.push(n[i]);
    }

    return result;
  };

  const getRoomByCenter = (center_id: string): LocationRoomTableInterface[] => {
    let n: LocationRoomTableInterface[] = [];
    if (props.roomTables) {
      n = props.roomTables.filter((element) => element.center_id === center_id);
    }

    let result: LocationRoomTableInterface[] = [],
      _ids: string[] = [];
    for (let i = 0; i < n.length; i++) {
      if (_ids.includes(n[i].room_id)) continue;
      _ids.push(n[i].room_id);
      result.push(n[i]);
    }

    return result;
  };

  return (
    <Fragment>
      {/* FILTER HEADER WITH LABELS ======================================= */}
      <div className="my-2 font-medium text-xl flex items-center gap-2">
        <FaFilter />
        Filter{" "}
        <section className={`${props.filterByCategory.length > 0 && "ml-2"}`}>
          {props.filterByCategory.length > 0 && (
            <button
              type="button"
              className="relative bg-gray-500 text-white px-3 py-1 rounded overflow-visible duration-300 
            text-xs font-bold group mr-2"
              onClick={() => selectDocCategory("all")}
              disabled={props.loadingRequest || props.loadingCandidates}
            >
              {props.filterByCategory}
              <div className="absolute top-0 right-0 -mt-2 -mr-2 px-1 py-1 bg-red-400 rounded-full opacity-0 group-hover:opacity-100">
                <MdClear />
              </div>
            </button>
          )}{" "}
          {props.filterByCategory.length > 0 &&
            props.filterBySubCategory.length > 0 && (
              <button
                type="button"
                className="relative bg-gray-500 text-white px-3 py-1 rounded overflow-visible duration-300 
            text-xs font-bold group mr-2"
                onClick={() => selectSubDocCategory("all")}
                disabled={props.loadingRequest || props.loadingCandidates}
              >
                {props.filterBySubCategory}
                <div className="absolute top-0 right-0 -mt-2 -mr-2 px-1 py-1 bg-red-400 rounded-full opacity-0 group-hover:opacity-100">
                  <MdClear />
                </div>
              </button>
            )}
        </section>
      </div>
      {/* END FILTER HEADER WITH LABELS ======================================= */}
      <div className="flex items-center md:flex-row flex-col gap-2">
        {props.selectedSetting === DocCheckingContentType.documents && (
          <div className="flex flex-col mb-3 md:w-1/6 w-full">
            <select
              className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn ${
                (props.loadingRequest || props.loadingCandidates) &&
                "cursor-wait"
              }`}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                setSelectedFilterType(
                  (e.target.value as FilterCategoryType) || ""
                )
              }
              disabled={props.loadingRequest || props.loadingCandidates}
            >
              <option value="">Filter type</option>
              {filterContent && filterContent.length > 0 ? (
                filterContent.map((item, i) => (
                  <option key={i} value={item.filterContentType}>
                    {item.caption}
                  </option>
                ))
              ) : (
                <option value="" disabled>
                  No Filter type
                </option>
              )}
            </select>
          </div>
        )}

        {/* FILTER BY LOCATION ================================ */}
        {(selectedFilterType === FilterCategoryType.location ||
          props.selectedSetting === DocCheckingContentType.names) && (
          <section className="w-full flex items-center gap-2">
            {props.distinctDistricts && (
              <div className="flex flex-col mb-3 md:w-1/6 w-full">
                <select
                  className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn ${
                    (props.loadingRequest || props.loadingCandidates) &&
                    "cursor-wait"
                  }`}
                  onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                    selectDistrict(e.target.value)
                  }
                  disabled={props.loadingRequest || props.loadingCandidates}
                  value={
                    props.thisDistrict ? props.thisDistrict.district_id : ""
                  }
                >
                  <option value="all">All Districts</option>
                  {props.distinctDistricts &&
                  props.distinctDistricts.length > 0 ? (
                    props.distinctDistricts.map((item, i) => (
                      <option
                        key={i + item.district_id}
                        value={item.district_id}
                      >
                        {item.district_name}
                      </option>
                    ))
                  ) : (
                    <option value="" disabled>
                      No District
                    </option>
                  )}
                </select>
              </div>
            )}

            {props.thisDistrict && (
              <div className="flex flex-col mb-3 md:w-1/6 w-full">
                <select
                  className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn ${
                    (props.loadingRequest || props.loadingCandidates) &&
                    "cursor-wait"
                  }`}
                  onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                    selectCenter(e.target.value)
                  }
                  disabled={props.loadingRequest}
                >
                  <option value="all">All center</option>
                  {props.thisDistrict &&
                  getCenterByDistrict(props.thisDistrict.district_id).length >
                    0 ? (
                    getCenterByDistrict(props.thisDistrict.district_id).map(
                      (item, i) => (
                        <option key={i + item.center_id} value={item.center_id}>
                          {item.center_name}
                        </option>
                      )
                    )
                  ) : (
                    <option value="" disabled>
                      No center
                    </option>
                  )}
                </select>
              </div>
            )}

            {props.thisCenter && (
              <div className="flex flex-col mb-3 md:w-1/6 w-full">
                <select
                  className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn ${
                    (props.loadingRequest || props.loadingCandidates) &&
                    "cursor-wait"
                  }`}
                  onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                    selectRoom(e.target.value)
                  }
                  disabled={props.loadingRequest}
                >
                  <option value="all">All room</option>
                  {props.thisCenter &&
                  getRoomByCenter(props.thisCenter.center_id).length > 0 ? (
                    getRoomByCenter(props.thisCenter.center_id).map(
                      (item, i) => (
                        <option key={i + item.room_id} value={item.room_id}>
                          {item.room_name}
                        </option>
                      )
                    )
                  ) : (
                    <option value="" disabled>
                      No Room
                    </option>
                  )}
                </select>
              </div>
            )}
          </section>
        )}
        {/* END FILTER BY LOCATION ================================ */}

        {/* FILTER BY DOCUMENT TYPE ================================ */}
        {(props.selectedSetting === DocCheckingContentType.documents &&
          selectedFilterType) === FilterCategoryType.document && (
          <section className="w-full flex items-center gap-2">
            {loading ? (
              <div
                className={`animate-pulse h-10 w-36 mb-3 bg-gray-300 rounded`}
              ></div>
            ) : (
              <div className="flex flex-col mb-3 md:w-1/6 w-full">
                <select
                  className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn ${
                    (props.loadingRequest || props.loadingCandidates) &&
                    "cursor-wait"
                  }`}
                  onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                    selectDocCategory(e.target.value)
                  }
                  disabled={props.loadingRequest || props.loadingCandidates}
                  value={props.filterByCategoryId}
                >
                  <option value="all">All Document type</option>
                  {props.documentCategories.documentCategory &&
                  importantDocTypes(
                    props.documentCategories.documentCategory,
                    props.candidateList,
                    props.countById
                  ).length > 0 ? (
                    importantDocTypes(
                      props.documentCategories.documentCategory,
                      props.candidateList,
                      props.countById
                    ).map((item, i) => (
                      <option
                        key={i + item.user_detail_category_id}
                        value={item.user_detail_category_id}
                      >
                        {item.title}
                      </option>
                    ))
                  ) : (
                    <option value="" disabled>
                      No Document type
                    </option>
                  )}
                </select>
              </div>
            )}
            {/* FILTER BY DOCUMENT SUB TYPE ================================ */}
            {subDocCategories &&
              props.filterByCategoryId.length > 0 &&
              props.filterByCategoryId !== "all" && (
                <div className="flex flex-col mb-3 md:w-1/6 w-full">
                  <select
                    className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none animate__animated animate__fadeIn ${
                      (props.loadingRequest || props.loadingCandidates) &&
                      "cursor-wait"
                    }`}
                    onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                      selectSubDocCategory(e.target.value)
                    }
                    disabled={props.loadingRequest || props.loadingCandidates}
                    value={props.filterBySubCategoryId}
                  >
                    <option value="all">
                      All type of {props.filterByCategory}
                    </option>
                    {subDocCategories && subDocCategories.length > 0 ? (
                      subDocCategories.map((item, i) => (
                        <option
                          key={i + item.user_detail_subcategory_id}
                          value={item.user_detail_subcategory_id}
                        >
                          {item.title}
                        </option>
                      ))
                    ) : (
                      <option value="" disabled>
                        No types of {props.filterByCategory}
                      </option>
                    )}
                  </select>
                </div>
              )}
          </section>
        )}
        {/* END FILTER BY DOCUMENT TYPE ================================ */}
      </div>
    </Fragment>
  );
};

export default React.memo(FilterForm);
