import React, { useState } from "react";
import { BiSort } from "react-icons/bi";
import { FaRegCheckCircle } from "react-icons/fa";
import { IoDocumentOutline } from "react-icons/io5";
import { MdInfoOutline, MdRemoveCircleOutline } from "react-icons/md";
import { RiEditBoxFill } from "react-icons/ri";
import Table from "../../../components/Table/Table";
import { toTwoDigit } from "../../../shared/dataTest";
import {
  CandidateDetailsInterface,
  CandidatesInterface,
} from "../../../shared/interfaces";
import { sortThisArray } from "../../../utils/functions";

type ContentByCandidateProps = {
  loadingCandidates: boolean;
  loadingRequest: boolean;
  candidateList: CandidatesInterface[];
  setCandidateId: (data: string) => void;
  setViewCandidate: (state: boolean) => void;
  countByStatus: (
    userDocs: CandidateDetailsInterface[],
    docStatus: "waiting" | "approved" | "rejected"
  ) => number;
  countById: (
    candidates: CandidatesInterface[],
    filterType: "docCategory" | "docSubCategory" | "docStatus" | "DISTRICT",
    filterValue: string,
    filterStatus?: "waiting" | "rejected" | "approved"
  ) => number;
  setLoadingImage: (state: boolean) => void;
};

const ContentByCandidate: React.FC<ContentByCandidateProps> = (props) => {
  const [isSorted, setIsSorted] = useState<boolean>(false);
  const [sortOrder, setSortOrder] = useState<"ASC" | "DESC">("ASC");
  const [sortProperty, setSortProperty] = useState<string>("");

  const tableData = () => {
    if (props.loadingCandidates) {
      return [
        <tr key={43243} className="border-b border-gray-200">
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap" colSpan={2}>
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
        </tr>,
        <tr key={43243343} className="border-b border-gray-200">
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap" colSpan={2}>
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
        </tr>,
        <tr key={4327543} className="border-b border-gray-200">
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap">
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
          <td className="p-3 text-center whitespace-nowrap" colSpan={2}>
            <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          </td>
        </tr>,
      ];
    }
    if (!props.candidateList.length) {
      return [
        <tr key={43243} className="border-b border-gray-200">
          <td className="p-3 text-center whitespace-nowrap" colSpan={6}>
            No Data Found
          </td>
        </tr>,
      ];
    }
    return sortThisArray(
      isSorted,
      props.candidateList,
      sortProperty,
      sortOrder
    ).map((candidate, index) => (
      <tr
        key={candidate.user_id + index}
        className="border-b border-gray-200 group hover:bg-gray-100 cursor-pointer"
        onClick={() => {
          !props.loadingRequest && props.setCandidateId(candidate.user_id);
          !props.loadingRequest && props.setViewCandidate(true);
          !props.loadingRequest && props.setLoadingImage(true);
          // setModalState(true);
        }}
      >
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left cursor-pointer">
          <span className="font-medium text-lg group-hover:font-semibold">
            {candidate.fname + " " + candidate.lname}
          </span>
        </td>
        <td className="p-3 text-left">
          <span>{candidate.n_id}</span>
        </td>
        <td className="py-3 px-5 text-left">
          {props.countByStatus(candidate.details, "approved")}
        </td>
        <td className="py-3 px-5 text-left">
          {props.countByStatus(candidate.details, "rejected")}
        </td>
        <td className="py-1 px-5 text-left">
          {props.countByStatus(candidate.details, "waiting") > 0 ? (
            <button
              className="flex items-center gap-3 border hover:border-2 border-white group-hover:border-yellow-800 group-hover:bg-yellow-100 rounded px-3 py-1 focus:outline-none"
              disabled={props.loadingRequest}
              onClick={() => {
                props.setCandidateId(candidate.user_id);
                props.setViewCandidate(true);
                // setModalState(true);
              }}
            >
              {candidate.details &&
                props.countByStatus(candidate.details, "waiting")}
              {candidate.details &&
                props.countByStatus(candidate.details, "waiting") > 0 && (
                  <RiEditBoxFill className="text-yellow-800 font-bold w-6 h-6 focus:outline-none" />
                )}
            </button>
          ) : (
            <span className="px-3 py-1">0</span>
          )}
        </td>
        <td className="text-right">
          <button
            className="bg-blue-500 group-hover:bg-blue-600 text-white font-bold py-1 px-1 inline-flex rounded focus:outline-none"
            title="View documents"
            disabled={props.loadingRequest}
            onClick={() => {
              props.setCandidateId(candidate.user_id);
              props.setViewCandidate(true);
              // setModalState(true);
            }}
          >
            <IoDocumentOutline className="w-5 h-5" />
            View docs
          </button>
        </td>
      </tr>
    ));
  };

  const setSort = (thisProperty: string) => {
    if (sortProperty === thisProperty && sortOrder === "ASC") {
      setSortProperty("");
      setIsSorted(false);
      setSortOrder("DESC");
    } else {
      setSortProperty(thisProperty);
      setIsSorted(true);
      setSortOrder("ASC");
    }
  };

  return (
    <div>
      <Table
        columns={[
          { columnTitle: "#", alignText: "left", padding: "p-3" },
          {
            columnTitle: (
              <div
                className="flex items-center gap-1 cursor-pointer"
                onClick={() => setSort("fname")}
              >
                <span>Names</span>
                <BiSort className="w-4 h-4" />
              </div>
            ),
            alignText: "left",
            padding: "p-3",
          },
          {
            columnTitle: (
              <div
                className="flex items-center gap-1 cursor-pointer"
                onClick={() => setSort("n_id")}
              >
                <span>National ID</span>
                <BiSort className="w-4 h-4" />
              </div>
            ),
            alignText: "left",
            padding: "p-3",
          },
          {
            columnTitle: (
              <div className="flex items-center text-blue-700 gap-1 text-base font-semibold">
                <FaRegCheckCircle className="w-4 h-4" />
                <span>Approved</span>
                {props.candidateList &&
                props.countById(
                  props.candidateList,
                  "docCategory",
                  "all",
                  "approved"
                ) > 0 ? (
                  <span>
                    (
                    {toTwoDigit(
                      props.countById(
                        props.candidateList,
                        "docCategory",
                        "all",
                        "approved"
                      )
                    )}
                    )
                  </span>
                ) : null}
              </div>
            ),
            alignText: "left",
            padding: "p-3",
          },
          {
            columnTitle: (
              <div className="flex items-center gap-1 text-gray-400 text-base font-semibold">
                <MdRemoveCircleOutline className="w-5 h-5" />
                <span>Rejected</span>
                {props.candidateList &&
                props.countById(
                  props.candidateList,
                  "docCategory",
                  "all",
                  "approved"
                ) > 0 ? (
                  <span>
                    (
                    {toTwoDigit(
                      props.countById(
                        props.candidateList,
                        "docCategory",
                        "all",
                        "rejected"
                      )
                    )}
                    )
                  </span>
                ) : null}
              </div>
            ),
            alignText: "left",
            padding: "p-3",
          },
          {
            columnTitle: (
              <div className="flex items-center gap-1 text-yellow-800 text-base font-semibold">
                <MdInfoOutline className="w-5 h-5" />
                <span>Pending</span>
                {props.candidateList &&
                props.countById(
                  props.candidateList,
                  "docCategory",
                  "all",
                  "approved"
                ) > 0 ? (
                  <span>
                    (
                    {toTwoDigit(
                      props.countById(props.candidateList, "docCategory", "all")
                    )}
                    )
                  </span>
                ) : null}
              </div>
            ),
            alignText: "left",
            padding: "p-3",
          },
          { columnTitle: "", alignText: "left", padding: "p-3" },
        ]}
        rows={tableData}
        pagination={true}
        search={true}
        thColor="gray"
        loading={props.loadingCandidates}
      />
    </div>
  );
};

export default React.memo(ContentByCandidate);
