import React, { Component } from "react";
import { AiOutlineFullscreen } from "react-icons/ai";
import { BiZoomIn, BiZoomOut } from "react-icons/bi";
import { CgImage } from "react-icons/cg";
import { GrNext, GrPrevious } from "react-icons/gr";
import { ImFolderOpen } from "react-icons/im";
import { IoMdClose } from "react-icons/io";
import { connect } from "react-redux";
import ViewPDF from "../../../../components/ViewPDF/ViewPDF";
import {
  getCandidateFileByIndex,
  GetDocTitle,
  getDocumentFileByIndex,
} from "../../../../interfaces/document_validation";
import { StoreState } from "../../../../reducers";
import { DocumentReducerInterface } from "../../../../reducers/document";
import { isEmptyOrSpaces } from "../../../../shared/dataTest";
import {
  CandidateDetailsInterface,
  CandidatesInterface,
  UserInterface,
} from "../../../../shared/interfaces";
import { API } from "../../../../utils/api";
import {
  fullScreen,
  getFileType,
  isFileImage,
  zoomInOut,
} from "../../../../utils/functions";
import {
  DocumentInArray,
  TableCandidatesInterface,
} from "../ContentByDocument";
import { DocCheckingContentType } from "../DocumentApplChecking";
import FooterInitial from "./FooterInitial";
import FooterResponse from "./FooterResponse";

interface AppProps {
  doc: CandidateDetailsInterface;
  candidate: CandidatesInterface;
  responseMsg: string;
  loadingRequest: boolean;
  loadingImage: boolean;
  setLoadingImage: (state: boolean) => void;
  selectedSetting: DocCheckingContentType | null;
  setResponseMsg: (state: string) => void;
  setResponse: (state: "approved" | "rejected") => void;
  handleVerify: () => Promise<void>;
  setError: (data: { element: string; msg: string }) => void;
  setViewDoc: (state: boolean) => void;
  setCandidateId: (state: string) => void;
  setDocumentId: (state: string) => void;
  setViewCandidate: (state: boolean) => void;
  actionTaken: "" | "approval" | "rejection";
  setActionTaken: (state: "" | "approval" | "rejection") => void;
  document_index: number;
  setDocument_index: (state: number | null) => void;
  error: {
    element: string;
    msg: string;
  };
  documentCategories: DocumentReducerInterface;
  candidateList: CandidatesInterface[];
  isTeachingDocument: boolean;
  setIsTeachingDocument: (state: boolean) => void;
  hasDisabilityDocument: boolean;
  setHasDisabilityDocument: (state: boolean) => void;
}

interface AppStates {
  isFullscreen: boolean;
}

class ApplFileView extends Component<AppProps, AppStates> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      isFullscreen: false,
    };
  }

  setSetFullscreen = (value: boolean) => {
    this.setState({ isFullscreen: value });
  };

  handleFullScreen = () => {
    if (this.state.isFullscreen) {
      // alert("closing");
      fullScreen("doc-img", "CLOSE");
      this.setSetFullscreen(false);
    } else {
      // alert("opening");
      this.setSetFullscreen(true);
      fullScreen("doc-img", "OPEN");
    }
  };

  handleNextPrev = (
    action: "NEXT" | "PREV",
    currentStatus: "approved" | "rejected" | "waiting" = "waiting"
  ) => {
    let detailsCopy = this.props.candidate.details.filter(
      (details) =>
        details.validation_status === "waiting" &&
        details.files &&
        details.files.length
    );
    let documentInArray =
      this.props.selectedSetting &&
      this.props.selectedSetting === DocCheckingContentType.documents
        ? DocumentInArray(
            this.props.documentCategories,
            this.props.candidateList
          )
        : detailsCopy;
    let newIndex: number = this.props.document_index;
    if (
      action === "NEXT" &&
      this.props.document_index <= documentInArray.length - 1
    ) {
      newIndex =
        currentStatus === "waiting"
          ? this.props.document_index + 1
          : this.props.document_index + 1;
      if (
        this.props.selectedSetting &&
        this.props.selectedSetting === DocCheckingContentType.documents
      ) {
        getDocumentFileByIndex(
          documentInArray as TableCandidatesInterface[],
          newIndex,
          this.props.setViewDoc,
          this.props.setDocumentId,
          this.props.setCandidateId,
          this.props.setDocument_index,
          this.props.setLoadingImage
        );
      } else {
        getCandidateFileByIndex(
          this.props.candidate,
          newIndex,
          this.props.setViewDoc,
          this.props.setDocumentId,
          this.props.setCandidateId,
          this.props.setDocument_index,
          this.props.setLoadingImage
        );
      }
    }
    if (action === "PREV" && this.props.document_index > 0) {
      newIndex = this.props.document_index - 1;
      if (
        this.props.selectedSetting &&
        this.props.selectedSetting === DocCheckingContentType.documents
      ) {
        getDocumentFileByIndex(
          documentInArray as TableCandidatesInterface[],
          newIndex,
          this.props.setViewDoc,
          this.props.setDocumentId,
          this.props.setCandidateId,
          this.props.setDocument_index,
          this.props.setLoadingImage
        );
      } else {
        getCandidateFileByIndex(
          this.props.candidate,
          newIndex,
          this.props.setViewDoc,
          this.props.setDocumentId,
          this.props.setCandidateId,
          this.props.setDocument_index,
          this.props.setLoadingImage
        );
      }
    }
    this.props.setIsTeachingDocument(false);
  };

  isNextAllowed = () => {
    if (this.props.selectedSetting) {
      if (this.props.selectedSetting === DocCheckingContentType.documents) {
        let dataList = DocumentInArray(
          this.props.documentCategories,
          this.props.candidateList
        );
        let validDataList = dataList.filter(
          (item) => item.validation_status === "waiting"
        );
        if (validDataList.length <= 1) {
          return false;
        }

        if (this.props.document_index <= dataList.length - 1) {
          if (
            dataList[this.props.document_index + 1] &&
            dataList[this.props.document_index + 1].validation_status ===
              "waiting"
          ) {
            return true;
          } else if (
            dataList[this.props.document_index] &&
            dataList[this.props.document_index].validation_status === "waiting"
          ) {
            return true;
          }
        } else {
          return false;
        }
      }
    }
    return false;
  };

  isPrevAllowed = () => {
    if (this.props.document_index > 0) {
      return true;
    }
    if (this.props.selectedSetting) {
      if (this.props.selectedSetting === DocCheckingContentType.documents) {
        let dataList = DocumentInArray(
          this.props.documentCategories,
          this.props.candidateList
        );
        if (
          dataList[this.props.document_index - 1] !== undefined &&
          !isEmptyOrSpaces(dataList[this.props.document_index - 1].files)
        ) {
          return true;
        } else {
          return false;
        }
      }
    }
    return false;
  };

  render() {
    return (
      <div className="bg-gray-50 h-full md:h-screen grid grid-cols-1 md:grid-cols-3">
        {/* image content */}
        {this.props.doc.files.length > 0 && this.props.loadingImage && (
          <div className="col-span-2 max-h-full md:max-h-screen overflow-y-auto relative group">
            <div className="w-full mx-auto mt-5 px-4">
              <div className="animate-pulse flex space-x-4">
                <div className="flex-1 space-y-4 py-4">
                  <div className="bg-gray-300 rounded h-72 flex items-center justify-center">
                    <h1 className="text-5xl text-gray-500 font-bold">
                      Loading{" "}
                      {isFileImage(getFileType(this.props.doc.files))
                        ? "image"
                        : "pdf"}
                      ...
                    </h1>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
        <div
          className={`col-span-2 h-auto md:max-h-screen overflow-y-auto relative bg-gray-200 group ${
            this.props.loadingImage && "hidden"
          }`}
        >
          {/* DISPLAY IMAGE OF PDF =================================== */}
          {this.props.doc.files.length > 0 ? (
            isFileImage(getFileType(this.props.doc.files)) ? (
              <div className="relative">
                <img
                  src={API.AUTH_FILES + this.props.doc.files}
                  alt=""
                  className={`w-full md:w-auto h-auto md:max-h-screen mx-auto ${
                    this.props.loadingImage && "hidden"
                  }`}
                  id="doc-img"
                  onLoad={() => this.props.setLoadingImage(false)}
                  onDoubleClick={() => this.handleFullScreen()}
                />
              </div>
            ) : (
              <ViewPDF
                file_url={API.AUTH_FILES + this.props.doc.files}
                class_name={`w-full h-screen md:max-h-screen mx-auto ${
                  this.props.loadingImage && "hidden"
                }`}
                frame_title={"application PDF file view"}
                setLoadingFile={this.props.setLoadingImage}
              />
            )
          ) : (
            <div className="text-center text-red-400 py-8 animate__animated animate__fadeIn">
              <CgImage className="mx-auto w-40 h-40" />
              <span className="font-semibold text-4xl">No Files Provided</span>
            </div>
          )}
          {/* END DISPLAY IMAGE OF PDF =================================== */}

          {/* ------------------------------------------------------------ */}

          {/* FILE VIEW CONTROLLERS =================================== */}
          <div className="flex items-center justify-between absolute top-0 w-full px-2 py-2">
            <button
              className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer"
              onClick={() => {
                this.props.selectedSetting !== DocCheckingContentType.names &&
                  this.props.setCandidateId("");
                this.props.selectedSetting === DocCheckingContentType.names &&
                  this.props.setViewCandidate(true);
                this.props.setViewDoc(false);
                this.props.setDocumentId("");
                this.props.setIsTeachingDocument(false);
              }}
              disabled={this.props.loadingRequest || this.props.loadingImage}
            >
              <IoMdClose className="h-6 w-6 text-gray-500 group-hover:text-gray-800" />
            </button>
            {this.props.doc.files.length > 0 &&
              isFileImage(getFileType(this.props.doc.files)) && (
                <div className="flex items-center gap-2 opacity-0 group-hover:opacity-100">
                  <button
                    className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                    onClick={() => zoomInOut("doc-img", "IN")}
                  >
                    <BiZoomIn className="h-6 w-6 text-gray-500" />
                  </button>
                  <button
                    className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                    onClick={() => zoomInOut("doc-img", "OUT")}
                  >
                    <BiZoomOut className="h-6 w-6 text-gray-500" />
                  </button>
                  <div
                    className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                    onClick={() => fullScreen("doc-img", "OPEN")}
                  >
                    <AiOutlineFullscreen className="h-6 w-6 text-gray-500" />
                  </div>
                </div>
              )}
          </div>
          {/* END FILE VIEW CONTROLLERS =================================== */}

          {/* ------------------------------------------------------------ */}

          {/* NEXT && PREV CONTROLLER ================================================= */}
          <div
            className={`absolute align-middle top-1/2 w-full flex ${
              !this.isPrevAllowed() ? "justify-end" : "justify-between"
            } z-20 opacity-0 group-hover:opacity-100 px-2`}
          >
            {this.isPrevAllowed() && (
              <button
                className={`inline-block bg-gray-100 cursor-pointer shadow-md rounded-full p-2 hover:bg-blue-50`}
                onClick={() => this.handleNextPrev("PREV")}
                disabled={this.props.loadingRequest || this.props.loadingImage}
              >
                <GrPrevious className="h-8 w-8 text-gray-500" />
              </button>
            )}
            {this.isNextAllowed() && (
              <button
                className={`inline-block bg-gray-100 cursor-pointer shadow-md rounded-full p-2 hover:bg-blue-50`}
                onClick={() =>
                  this.handleNextPrev("NEXT", this.props.doc.validation_status)
                }
                disabled={this.props.loadingRequest || this.props.loadingImage}
              >
                <GrNext className="h-8 w-8 text-gray-500" />
              </button>
            )}
          </div>
          {/* END NEXT && PREV CONTROLLER ================================================= */}
        </div>
        {/* end image content */}

        {/* -------------------------------------------- */}

        <section className="relative">
          <div className="w-full flex md:flex-col flex-col justify-between items-start gap-2">
            {/* CANDIDATE INFO PART */}
            <div className="flex items-center gap-4 w-full">
              {/* doc title & status */}
              <section className="p-3 w-full">
                <div className="flex items-center gap-2 border-b-2 my-2 py-1">
                  <ImFolderOpen className="text-blue-600 text-xl" />
                  <h2 className="uppercase tracking-wide text-xl font-semibold text-gray-700">
                    Document validation
                  </h2>
                </div>
                <div className="flex flex-row items-center justify-left gap-2 mb-2">
                  <span className="text-gray-500 italic">Doc Title: </span>
                  <span className="font-bold text-2xl">
                    {
                      GetDocTitle(this.props.documentCategories, this.props.doc)
                        .main
                    }
                  </span>
                </div>
                <div className="flex flex-row items-center justify-left gap-2 mb-2">
                  <span className="text-gray-500 italic">Names: </span>
                  <span className="font-medium text-lg">
                    {this.props.candidate.fname +
                      " " +
                      this.props.candidate.lname}
                  </span>
                </div>
                <section className="animate__animated animate__fadeIn">
                  <div className="flex flex-row items-center justify-left gap-2 mb-2">
                    <span className="text-gray-500 italic">National ID: </span>
                    <span className="font-medium text-lg">
                      {this.props.candidate.n_id}
                    </span>
                  </div>
                  <div className="flex flex-row items-center justify-left gap-2 mb-2">
                    <span className="text-gray-500 italic">Phone: </span>
                    <span className="font-medium text-lg">
                      {this.props.candidate.phone_number}
                    </span>
                  </div>
                  <div className="flex flex-row items-center justify-left gap-2 mb-2">
                    <span className="text-gray-500 italic">Email: </span>
                    <span className="font-medium text-lg">
                      {this.props.candidate.email}
                    </span>
                  </div>
                  <div className="flex flex-row items-center justify-left gap-2 mb-2">
                    <span className="text-gray-500 italic">District: </span>
                    <span className="font-medium text-lg">
                      {this.props.candidate.district}
                    </span>
                  </div>
                </section>
                {/* status */}
                <div className="flex items-center gap-1">
                  <span className="text-gray-500 italic">Status:</span>
                  <span
                    className={`text-${
                      this.props.doc.validation_status === "waiting"
                        ? "yellow"
                        : this.props.doc.validation_status === "approved"
                        ? "blue"
                        : "gray"
                    }-700 font-semibold text-lg`}
                  >
                    {this.props.doc.validation_status === "waiting"
                      ? "Pending"
                      : this.props.doc.validation_status === "approved"
                      ? "Approved"
                      : "Rejected"}
                  </span>
                </div>
                {/* end status */}
              </section>
              {/* end doc title & status */}
            </div>
            {/* END CANDIDATE INFO PART */}

            {/* ################################################ */}

            {this.props.doc.files &&
              (!this.props.actionTaken ? (
                <FooterInitial
                  docStatus={this.props.doc.validation_status}
                  setActionTaken={this.props.setActionTaken}
                  loading={this.props.loadingRequest}
                  setResponse={this.props.setResponse}
                  loadingImage={this.props.loadingImage}
                />
              ) : (
                <FooterResponse
                  disability={
                    this.props.candidate.disability
                      ? this.props.candidate.disability
                      : false
                  }
                  in_education={
                    this.props.candidate.in_education
                      ? this.props.candidate.in_education
                      : false
                  }
                  setActionTaken={this.props.setActionTaken}
                  actionTaken={this.props.actionTaken}
                  setResponseMsg={this.props.setResponseMsg}
                  responseMsg={this.props.responseMsg}
                  loadingRequest={this.props.loadingRequest}
                  handleVerify={this.props.handleVerify}
                  error={this.props.error}
                  setError={this.props.setError}
                  isTeachingDocument={this.props.isTeachingDocument}
                  setIsTeachingDocument={this.props.setIsTeachingDocument}
                  hasDisabilityDocument={this.props.hasDisabilityDocument}
                  setHasDisabilityDocument={this.props.setHasDisabilityDocument}
                />
              ))}
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = ({
  document,
}: StoreState): {
  documentCategories: DocumentReducerInterface;
} => {
  return { documentCategories: document };
};

export default React.memo(connect(mapStateToProps, {})(ApplFileView));
